import { Chip } from '@mui/material'
import { memo } from 'react'

export const EmployeeChip = memo(({ employee }) => {
    return (
        <Chip
            label={employee.employee}
            color="#bababa"
            sx={{
                height: '15px',
                borderRadius: '16px',
                position: 'absolute',
                left: '10px',
                '& .MuiChip-label': {
                    padding: '0 8px',
                },
                opacity: 0,
                animation: 'fadeIn 0.3s ease-in forwards',
                '@keyframes fadeIn': {
                    '0%': {
                        opacity: 0,
                        transform: 'translateX(-10px)',
                    },
                    '100%': {
                        opacity: 1,
                        transform: 'translateX(0)',
                    },
                },
            }}
        />
    )
})
